/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd,
button,
input {
  margin: 0;
  padding: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --cyan: hsl(172, 67%, 45%);
  --veryDarkCyan: hsl(183, 100%, 15%);
  --darkGrayishCyan: hsl(186, 14%, 43%);
  --darkGrayishCyan2: hsl(186, 14%, 56%);
  --darkGrayishCyan3: hsl(184, 19%, 68%);
  --lightGrayishCyan: hsl(185, 41%, 84%);
  --lightGrayishCyan2: hsl(189, 41%, 97%);
  --white: hsl(0, 0%, 100%);
  --error: hsl(13, 70%, 61%);
}

:root {
  --font-space: "Space Mono", monospace, "Public Sans", sans-serif;
}

body {
  background-color: var(--lightGrayishCyan);
  min-height: 100vh;
}

.wrapper {
  row-gap: 4rem;
}

.logo {
  justify-self: center;
}

.label {
  display: block;
}

.heading-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.error {
  color: var(--error);
  font-size: 1.6rem;
  font-weight: 700;
  display: none;
}

html {
  font-size: 62.5%;
}

body {
  font-family: var(--font-space);
}

.heading {
  font-size: 1.6rem;
  font-weight: 700;
  color: var(--darkGrayishCyan);
}

.button {
  height: 4.8rem;
  border: none;
  border-radius: 5px;
  font-weight: 700;
  cursor: pointer;
}
.button-tip {
  font-size: 2.4rem;
  background-color: var(--veryDarkCyan);
  color: var(--white);
}

.reset-button {
  font-size: 2rem;
  font-weight: 700;
  text-transform: uppercase;
  color: var(--veryDarkCyan);
  border: none;
  background-color: var(--cyan);
  width: 100%;
  border-radius: 5px;
  padding: 1rem 0;
}

.calculator {
  max-width: 37.5rem;
  width: 100%;
  margin: 0 auto;
  padding: 3.2rem;
  background-color: var(--white);
  border-radius: 25px;
  justify-content: center;
}
.calculator__input {
  height: 4.8rem;
  padding-right: 1.7rem;
  text-align: right;
  border: none;
  border-radius: 5px;
  background-color: var(--lightGrayishCyan2);
  font-weight: 700;
  color: var(--veryDarkCyan);
}
.calculator__input:focus {
  outline: 2px solid var(--cyan);
}
.calculator__input::placeholder {
  color: var(--darkGrayishCyan3);
}
.calculator__input--num {
  font-size: 2.4rem;
  background-position: 1.7rem;
  background-repeat: no-repeat;
}
.calculator__input--dollar {
  background-image: url("../images/icon-dollar.svg");
}
.calculator__input--person {
  background-image: url("../images/icon-person.svg");
}
.calculator__input--custom {
  width: 100%;
  font-size: 2.4rem;
}
.calculator__result {
  background-color: var(--veryDarkCyan);
  padding: 3.7rem 2.4rem 2.4rem;
  border-radius: 15px;
}
.calculator__result-num {
  font-size: 3.2rem;
  font-weight: 700;
  color: var(--cyan);
}
.calculator__amount {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2rem;
}
.calculator__amount:not(:first-of-type) {
  margin-bottom: 3.2rem;
}
.calculator__text {
  display: block;
  font-size: 1.6rem;
  font-weight: 700;
  color: var(--white);
}
.calculator__text-divide {
  font-size: 1.3rem;
  color: var(--darkGrayishCyan);
}

.grid {
  display: grid;
}
.grid-button {
  grid-template-columns: repeat(2, 1fr);
}

.gap-xs {
  row-gap: 0.6rem;
}

.gap-sm {
  row-gap: 1.6rem;
  column-gap: 1.6rem;
}

.gap-md {
  row-gap: 2rem;
}

.gap-lg {
  row-gap: 3.2rem;
}